export function prepeareOrderData({
  _id,
  createdAt,
  updatedAt,
  type,
  number,
  status,
  comment,
  createdBy = {},
  updatedBy = {},
  payment = {},
  invoices = {},
  ...others
}) {
  let data = {
    _id,
    createdAt,
    createdBy,
    updatedAt,
    updatedBy,
    orderType: type,
    orderStatus: status,
    orderNumber: number,
    payment,
    invoices,
    comment,
    ...others,
  };
  return data;
}
